
#app {
  font-family: "PingFang SC", Arial, sans-serif;
  overflow: hidden;
}
body {
  background-color: #eeeff3;
  color: #333;
}
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  overflow-x: hidden;
}
html ul,
body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
html .app-content,
body .app-content {
  height: 100%;
}
html .header,
body .header {
  background: #333;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #fff;
}
vux-loading {
  z-index: 999;
}
.animated {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
}
}
@keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
100% {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
}
}
.fadeInUp {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
}



